.custom-select__wrapper {
  position: relative;
  z-index: 12;
  max-width: 96px;
  min-width: 96px;
  width: 100%;
}
.custom-select__block {
  position: relative;
  display: inline-block;
  width: 100%;
}
.custom-select__block select {
  display: none;
}
.custom-select__block:before {
  content: attr(data-content);
  display: block;
  width: 100%;
  padding: 8px 10px;
  padding-bottom: 15px;
  color: #fff;
  border-radius: 20px;
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.25);
  background-color: #4777F2;
  cursor: pointer;
  text-align: center;
  font-family: Public Sans, sans-serif;
}
.custom-select__block::after {
  position: absolute;
  left: 50%;
  bottom: 4px;
  z-index: 3;
  width: 8px;
  height: 8px;
  content: " ";
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  pointer-events: none;
  transform: rotate(45deg) translateX(-50%);
  transition: transform 0.3s, top 0.3s;
}
.custom-select__inner:focus + .custom-select::before {
  border-color: #77aaff;
}
.custom-select__options {
  position: absolute;
  z-index: 112;
  bottom: 0px;
  left: 0;
  width: 100%;
  border-radius: 20px;
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.25);
  background: rgb(255, 255, 255);
  padding-bottom: 16px;
  display: none;
  transition: all 0.3s ease;
}
.custom-select__options::after {
  position: absolute;
  left: 50%;
  bottom: 0px;
  z-index: 3;
  width: 8px;
  height: 8px;
  content: " ";
  border-left: 2px solid #434343;
  border-top: 2px solid #434343;
  pointer-events: none;
  transform: rotate(45deg) translateX(-50%);
  transition: transform 0.3s, top 0.3s;
}
.custom-select__options.visible {
  display: block;
}
.custom-select__item {
  cursor: pointer;
  padding: 10px;
  font-family: Public Sans, sans-serif;
}
.custom-select__item.active {
  background-color: #4777F2;
  color: #fff;
}
.custom-select__item.active:first-child {
  border-radius: 20px 20px 0 0;
}
.custom-select__item.active:not(:first-child) {
  border-radius: 20px;
}
